import React, {
  ReactNode,
  Dispatch,
  SetStateAction,
  createContext,
  useEffect,
  useState,
} from 'react';
import { useContext } from 'react';

type Theme = 'light' | 'dim' | 'dark';

type Value = {
  theme: Theme;
  setTheme?: Dispatch<SetStateAction<Theme>>;
};

type Props = {
  children: ReactNode;
};

const ThemeContext = createContext<Value>({ theme: 'light' });

const ThemeProvider = ({ children }: Props) => {
  // console.log('theme provider is loading');
  const [theme, setTheme] = useState<Theme>('light');

  useEffect(() => {
    const getInitialColorTheme = (): Theme => {
      if (typeof window !== `undefined`) {
        const userPreference = window.localStorage.getItem(
          'theme'
        ) as null | Theme;

        if (userPreference && typeof userPreference === 'string') {
          return userPreference;
        }

        const systemPreference = window.matchMedia(
          '(prefers-color-scheme: dark)'
        ).matches
          ? 'dim'
          : 'light';

        if (systemPreference) {
          return systemPreference;
        }

        return 'light';
      }

      return 'light';
    };

    setTheme(getInitialColorTheme());
  }, []);

  useEffect(() => {
    window.localStorage.setItem('theme', theme);
  }, [theme]);

  return (
    <ThemeContext.Provider value={{ theme, setTheme }}>
      {children}
    </ThemeContext.Provider>
  );
};

export const useTheme = () => {
  const { theme, setTheme } = useContext(ThemeContext);

  return { theme, setTheme };
};

export default ThemeProvider;
