import React from 'react';
import styled from '@emotion/styled';
import { Link } from 'gatsby';
import LogoXSvg from '../../assets/logo/logo-x-svg';
import LogoWordSvg from '../../assets/logo/logo-word-svg';
import ContentsArea from './contentsarea';
import { MoonFill, Stars, SunFill } from 'react-bootstrap-icons';
import { useTheme } from '../../store/theme_provider';
import IconSun from '../../assets/icons/icon-sun';

const Border = styled.header`
  border-bottom: ${props => `1px solid ${props.theme.color.line}`};
  box-shadow: ${props => `0 0 10px ${props.theme.color.shadow}`};
`;

const Container = styled(ContentsArea)`
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 5rem;
`;

const Logo = styled.div``;

const LogoLink = styled(Link)`
  display: flex;
  align-items: center;

  div {
    display: flex;
    justify-content: center;
    align-items: center;
  }

  div[class='logo-symbol'] {
    padding: 3px;
    svg {
      height: 18px;
      fill: ${props => props.theme.color.textMain};
    }
  }

  div[class='logo-word'] {
    margin-left: 8px;
    padding-top: 5px;
    svg {
      height: 23px;
      fill: ${props => props.theme.color.textMain};
    }
  }

  &:hover,
  &:focus {
    outline: none;

    div[class='logo-symbol'] {
      background: ${props => props.theme.color.highlight};
      svg {
        fill: ${props => props.theme.color.backMain};
      }
    }

    div[class='logo-word'] {
      svg {
        fill: ${props => props.theme.color.highlight};
      }
    }
  }
`;

const ThemeButtons = styled.div``;

const ThemeButton = styled.button`
  display: flex;
  justify-content: center;
  align-items: center;
  width: 32px;
  height: 32px;
  border-radius: 32px;
  transition: background 150ms;

  svg {
    width: 18px;
    height: 18px;
    fill: ${props => props.theme.color.textSub};
    transition: fill 150ms;
  }

  &:hover,
  &:focus {
    outline: none;
    background: ${props => props.theme.color.backSub};
    svg {
      fill: ${props => props.theme.color.highlight};
    }
  }
`;

function Header() {
  const { theme, setTheme } = useTheme();

  const onThemeButtonClick = () => {
    if (theme == null || setTheme == null) return;

    switch (theme) {
      case 'light':
        setTheme('dim');
        return;
      case 'dim':
        setTheme('dark');
        return;
      case 'dark':
        setTheme('light');
        return;
      default:
        setTheme('light');
        return;
    }
  };

  return (
    <Border>
      <Container>
        <Logo>
          <LogoLink to="/">
            <div className="logo-symbol">
              <LogoXSvg />
            </div>
            <div className="logo-word">
              <LogoWordSvg />
            </div>
          </LogoLink>
        </Logo>
        <ThemeButtons>
          <ThemeButton onClick={onThemeButtonClick}>
            {theme === 'light' && <IconSun />}
            {theme === 'dim' && <Stars />}
            {theme === 'dark' && <MoonFill />}
          </ThemeButton>
        </ThemeButtons>
      </Container>
    </Border>
  );
}

export default Header;
